// Donnes prives, stockes uniquement en local
import localforage from 'localforage'

class StockageLocal {
    constructor() {
        this.localforage = localforage
    }

    getProfilActuel() {
        return localforage.getItem('profil')
    }

    setProfilActuel(nom) {
        return localforage.setItem('profil', nom)
    }

    getProfils() {
        return localforage
            .keys()
            .then((noms) => {
                return noms.filter((nom) => nom != 'profil')
            })
            .then((noms) => {
                return noms.sort((a) => {
                    // Make sure we return my profile first.
                    return a !== 'mes_infos'
                })
            })
    }

    supprimerTout() {
        return localforage
            .dropInstance()
            .then(() => {
                console.debug('Les donnes personnelles ont t supprimes')
            })
            .catch((error) => {
                console.error(
                    `Erreur lors de la suppression de toutes les donnes personnelles`
                )
                console.error(error)
            })
    }

    supprimer(nom) {
        return localforage
            .removeItem(nom)
            .then(() => {
                console.debug(`Les donnes personnelles ont t supprimes (${nom})`)
                return
            })
            .catch((error) => {
                console.error(
                    `Erreur lors de la suppression des donnes personnelles (${nom})`
                )
                console.error(error)
            })
    }

    charger(profil) {
        return localforage
            .getItem(profil.nom)
            .then((data) => {
                if (data !== null) {
                    console.debug(`Donnes locales (${profil.nom})`)
                    console.log(data)
                    profil.fillData(data)
                } else {
                    console.debug(
                        `Pas de donnes locales pour linstant (${profil.nom})`
                    )
                    profil.resetData()
                }
                return profil
            })
            .catch((error) => {
                console.error(
                    `Erreur de chargement des donnes locales (${profil.nom})`
                )
                console.error(error)
            })
    }

    enregistrer(profil) {
        return localforage
            .setItem(profil.nom, profil.getData())
            .then((data) => {
                console.debug(
                    `Les rponses au questionnaire ont bien t enregistres (${profil.nom})`
                )
                console.debug(data)
            })
            .catch((error) => {
                console.error(
                    `Les rponses au questionnaire nont pas pu tre enregistres (${profil.nom})`
                )
                console.error(error)
            })
    }
}

module.exports = {
    StockageLocal,
}
